

function addVolunteers(state, stat, mob) {

  const ru = state.filter((region) => region.country === 'Russia')
  const uk = state.filter((region) => region.country === 'Ukraine')

  const multiplierRu = stat.RuSpirit / 100
  const multiplierUk = stat.UkSpirit / 100

  const multiMob = mob === 'fm' ? 60 : mob === 'mob' ? 3 : mob === 'wag' ? 2 : 1;

  console.log([ru, uk])

  const upRu = {
    foot: (4 * multiplierRu) * multiMob,
    artillery: (3 * multiplierRu) * multiMob,
    aviation: (2 * multiplierRu) * multiMob,
    pvo: (1 * multiplierRu) * multiMob,
  }

  const upUk = {
    foot: (3 * multiplierUk),
    artillery: (2 * multiplierUk),
    aviation: (1 * multiplierUk),
    pvo: (0.5 * multiplierUk),
  }

  const sumUpRu = [
    Math.round(upRu.foot * ru.length),
    Math.round(upRu.artillery * ru.length),
    Math.round(upRu.aviation * ru.length),
    Math.round(upRu.pvo * ru.length)
  ];
  const sumUpUk = [
    Math.round(upUk.foot * uk.length),
    Math.round(upUk.artillery * uk.length),
    Math.round(upUk.aviation * uk.length),
    Math.round(upUk.pvo * uk.length)
  ];

  const newRuArmy = ru.map((region) => {
    return {
      ...region,
      army: {
        ...region.army,
        foot: Math.round(region.army.foot + upRu.foot),
        artillery: Math.round(region.army.artillery + upRu.artillery),
        aviation: Math.round(region.army.aviation + upRu.aviation),
        pvo: Math.round(region.army.pvo + upRu.pvo),
      }
    }
  })

  const newUkArmy = uk.map((region) => {
    return {
      ...region,
      army: {
        ...region.army,
        foot: Math.round(region.army.foot + upUk.foot),
        artillery: Math.round(region.army.artillery + upUk.artillery),
        aviation: Math.round(region.army.aviation + upUk.aviation),
        pvo: Math.round(region.army.pvo + upUk.pvo),
      }
    }
  })

  //console.log('ru areas: ', ru)
  //console.log('uk areas: ', uk)
  //console.log(newRuArmy, newUkArmy)
  //console.log(sumUpRu, sumUpUk)

  return [newRuArmy, newUkArmy, sumUpRu, sumUpUk]

}

export { addVolunteers }
