import React, { useEffect, useState } from 'react'
import { useSelector, useDispatch } from 'react-redux';

import { newMainState, resetMState } from '../../store/slices/mainSlice';
import { newStatState, resetSState, soundToggle, musicToggle } from '../../store/slices/statSlice';

import { maindata } from '../../data/maindata'
import { statdata } from '../../data/statdata'

import sounds from '../../assets/icons/volume.png'
import nosounds from '../../assets/icons/novolume.png'


import cs from './styles.module.scss'

export const MainMenu = ({ start, setStart, clickToMenu, setMusicClick, musicMenu, lng, setLng, switchLng }) => {

  const [settings, setSettings] = useState(false);
  const [raiting, setRaiting] = useState(false);
  const [noAuth, setNoAuth] = useState(false);

  const [dataRaiting, setDataRaiting] = useState({
    entries
      : [
        {
          extraData: '', score: 86, rank: 1, player: {
            publicName
              :
              "Maximka Egorov"
          }, formattedScore: '21'
        }
        ,
        {
          extraData: '', score: 91, rank: 2, player: {
            publicName
              :
              "Andrey Koptsov"
          }, formattedScore: '12'
        },
        {
          extraData: '', score: 105, rank: 3, player: {
            publicName
              :
              "Sasha Makarov"
          }, formattedScore: '2'
        },
        {
          extraData: '', score: 121, rank: 4, player: {
            publicName
              :
              ""
          }, formattedScore: '2'
        },
      ]
  });


  const state = useSelector(state => state.main);
  const stat = useSelector(state => state.stat)

  const dispatch = useDispatch();

  const isLocalStorage = () => localStorage.getItem("rukrStateMain01") && localStorage.getItem("rukrStateStat02")
    ? true : false;

  const newGame = () => {
    localStorage.clear();
    dispatch(resetMState())
    dispatch(resetSState())
    setStart(!start);
    if (stat.settings.sound) {
      clickToMenu()
    }
  }

  //console.log(isLocalStorage())

  function soundTogglF() {
    if (!stat.settings.sound) {
      clickToMenu()
    }
    //console.log('snd')
    dispatch(soundToggle())

  }

  function musicTogglF() {

    setMusicClick(true)

    if (stat.settings.sound) {
      clickToMenu()
      musicMenu()
    }
    //console.log('msc')
    dispatch(musicToggle())

    console.log(stat.settings.music)

  }

  function getRaiting() {
    if (stat.settings.sound) {
      clickToMenu()
    }

    /* ysdk.getLeaderboards()
      .then(lb => lb.getLeaderboardPlayerEntry('leaderboard2021'))
      .then(res => console.log(res))
      .catch(err => {
        if (err.code === 'LEADERBOARD_PLAYER_NOT_PRESENT') {
          // ,       
        }
      }); */


    ysdk.getLeaderboards()
      .then(lb => {
        lb.getLeaderboardEntries('mainFinal', { quantityTop: 10, includeUser: true, quantityAround: 3 })
          .then(res => setDataRaiting(res));
      });



    setRaiting(true)
  }

  function Auth() {
    ysdk.auth.openAuthDialog()
  }

  return (
    <div className={start ? cs.menu : cs.menu_active}>

      {!settings && !raiting &&
        <div className={cs.main}>
          <button
            disabled={!isLocalStorage()}
            className={`${cs.start} `}
            onClick={() => {
              if (stat.settings.sound) {
                clickToMenu()
              }
              setStart(!start)
            }}
          > {switchLng('continue')} </button>
          <button
            className={cs.start}
            onClick={() => newGame()}
          > {switchLng('game')} </button>
          <h1
            className={cs.start}
            onClick={() => {
              if (stat.settings.sound) {
                clickToMenu()
              }
              setSettings(!settings)
            }}
          > {switchLng('settings')} </h1>
          <button
            disabled={true}
            style={{ width: '95%' }}
            className={cs.start}
            onClick={() => {
              if (stat.settings.sound) {
                clickToMenu()
              }
              getRaiting()
            }}
          > {switchLng('rating')} </button>

          <div className={cs.lang}
          >
            <h2 className={`${cs.btn_lang} + ' ' + ${lng === 'en' && cs.active}`}
              onClick={() => setLng('en')}
            >EN</h2>
            <h2>&nbsp;|&nbsp;</h2>
            <h2 className={`${cs.btn_lang} + ' ' + ${lng === 'ru' && cs.active}`}
              onClick={() => setLng('ru')}
            >RU</h2>
          </div>


        </div>
      }

      {
        settings &&
        <div className={cs.settings}>
          <h1 style={{ fontSize: '2em' }}>{switchLng('settings')}</h1>
          <div className={cs.settings_container}>
            <div className={cs.sounds}
              onClick={() => soundTogglF()}
            >
              <h2 style={{ color: 'lightgrey' }}>{switchLng('sounds')}</h2>
              <img style={{ width: '3em', height: '3em' }} src={stat.settings.sound ? sounds : nosounds} />
            </div>
            <div className={cs.music}
              onClick={() => musicTogglF()}
            >
              <h2 style={{ color: 'lightgrey' }}>{switchLng('music')}</h2>
              <img style={{ width: '3em', height: '3em' }} src={stat.settings.music ? sounds : nosounds} />
            </div>

          </div>

          <button
            style={{ display: 'block', margin: '5px auto 5px auto', fontSize: '1.7em' }}
            className={cs.start}
            onClick={() => {
              if (stat.settings.sound) {
                clickToMenu()
              }
              setSettings(!settings)
            }
            }
          >{switchLng('accept')}</button>
        </div>
      }

      {
        raiting &&
        <div className={cs.settings}>
          <h1 style={{ fontSize: '1.5em' }}>:</h1>
          <div className={cs.settings_container}>

            <table>
              <tbody>

                {dataRaiting.entries.map((el) => (
                  <tr key={el.rank}>
                    <td
                      className={
                        el.rank === 1 ? cs.rank1
                          : el.rank === 2
                            ? cs.rank2
                            : el.rank === 3
                              ? cs.rank3 : cs.ranks
                      }

                    >{el.player.publicName.length > 0
                      ? `${el.player.publicName}  ${el.score} ()`
                      : `   ${el.score} ()`}</td>
                    {/* <td>{el.score}</td> */}
                  </tr>

                ))}

                {dataRaiting.entries[0].player.publicName === 'Maximka Egorov' &&
                  <div>
                    <br />
                    <p><span
                      style={{ color: 'lightgreen', fontWeight: 'bold' }}
                      onClick={Auth}

                    ></span>  .</p>
                    <p>   .</p>
                  </div>
                }

              </tbody>
            </table>
          </div>
          <button
            style={{ display: 'block', margin: '5px auto 5px auto', fontSize: '1.6em' }}
            className={cs.start}
            onClick={() => {
              if (stat.settings.sound) {
                clickToMenu()
              }
              setRaiting(!raiting)
            }}
          ></button>
        </div>
      }


    </div >
  )
}
