import React, { useEffect, useState } from 'react'
import { battleResult } from '../../../../../AI/battle';
import { useDispatch, useSelector } from 'react-redux';

import { goAttackArmy, changeArea, swapArmy } from '../../../../../store/slices/mainSlice';
import { attackDown } from '../../../../../store/slices/statSlice';

import useSound from 'use-sound';

import win from '../../../../../assets/sound/win.mp3'
import lose from '../../../../../assets/sound/lose.mp3'


import cs from './AttackArmy.module.scss'


export const AttackArmy = ({ fromToMove, popUpMove, setPopUpMove, defaultPopUp, setDefaultPopUp, clickToMenu, switchLng, lng }) => {
  console.log(defaultPopUp)

  const [winPlay] = useSound(win);
  const [losePlay] = useSound(lose);


  const [isStart, setIsStart] = useState(true);

  const states = useSelector((state) => state.main)
  const stat = useSelector((state) => state.stat)
  const dispatch = useDispatch();

  function goBattle() {
    const newData = battleResult(fromToMove, stat.RuSpirit, stat.UkSpirit)
    console.log(newData)
    console.log(fromToMove)

    newData.map(area => {
      const id = area.id;
      const army = area.army;
      dispatch(goAttackArmy({ id, army }));
    })

    const sumDefer = newData[1].army.foot
      + newData[1].army.artillery
      + newData[1].army.aviation
      + newData[1].army.pvo;
    console.log(sumDefer)

    const losses = [
      {
        foot: fromToMove[0].army.foot - newData[0].army.foot,
        technic: (fromToMove[0].army.artillery + fromToMove[0].army.aviation + fromToMove[0].army.pvo) - (newData[0].army.artillery + newData[0].army.aviation + newData[0].army.pvo)
      },
      {
        foot: fromToMove[1].army.foot - newData[1].army.foot,
        technic: (fromToMove[1].army.artillery + fromToMove[1].army.aviation + fromToMove[1].army.pvo) - (newData[1].army.artillery + newData[1].army.aviation + newData[1].army.pvo)
      }
    ];

    if (sumDefer < 6) {
      dispatch(changeArea(newData[1].id))
      dispatch(swapArmy([newData[0].id, newData[1].id]))
      console.log()
      setDefaultPopUp((prev) => {
        return {
          ...prev,
          type: 'you',
          isWin: true,
          areas: [lng === 'en' ? newData[0].nameEn : newData[0].name, lng === 'en' ? newData[1].nameEn : newData[1].name],
          title: `${switchLng('captured')} ${lng === 'en' ? newData[1].nameEn : newData[1].name}`,
          data: losses,
          btn: switchLng('wellDone')
        }
      })
      if (stat.settings.sound) {
        winPlay()
      }



    } else {

      setDefaultPopUp((prev) => {
        return {
          ...prev,
          type: 'you',
          isWin: false,
          areas: [lng === 'en' ? newData[0].nameEn : newData[0].name, lng === 'en' ? newData[1].nameEn : newData[1].name],
          title: `${lng === 'en' ? newData[1].nameEn : newData[1].name} ${lng === 'en' ? 'not captured' : ' '}`,
          data: losses,
          btn: switchLng('okay')
        }
      })
      if (stat.settings.sound) {
        losePlay()
      }
    }
  }

  return (
    <div className={cs.attack_container} >
      <div className={cs.line_army}>
        <h2 className={cs.name_army}>{switchLng('foot')}</h2>
        <div className={cs.values}></div>
        <p className={cs.value}>{fromToMove[0].army.foot}{lng === 'en' ? 'k' : '.'}</p>
        <p className={cs.value}><span style={{ color: 'lightcoral' }}>x</span></p>
        <p className={cs.value}>{fromToMove[1].army.scouting ? fromToMove[1].army.foot : '???'}{lng === 'en' ? 'k' : '.'}</p>
      </div>

      <div className={cs.line_army}>
        <h2 className={cs.name_army}>{switchLng('tanks')}</h2>

        <p className={cs.value}>{fromToMove[0].army.artillery}</p>
        <p className={cs.value}><span style={{ color: 'lightcoral' }}>x</span></p>
        <p className={cs.value}>{fromToMove[1].army.scouting ? fromToMove[1].army.artillery : '???'}</p>
      </div>

      <div className={cs.line_army}>
        <h2 className={cs.name_army}>{switchLng('artillery')}</h2>

        <p className={cs.value}>{fromToMove[0].army.pvo}</p>
        <p className={cs.value}><span style={{ color: 'lightcoral' }}>x</span></p>
        <p className={cs.value}>{fromToMove[1].army.scouting ? fromToMove[1].army.pvo : '???'}</p>
      </div>

      <div className={cs.line_army}>
        <h2 className={cs.name_army}>{switchLng('aviation')}</h2>

        <p className={cs.value}>{fromToMove[0].army.aviation}</p>
        <p className={cs.value}><span style={{ color: 'lightcoral' }}>x</span></p>
        <p className={cs.value}>{fromToMove[1].army.scouting ? fromToMove[1].army.aviation : '???'}</p>
      </div>

      {isStart && <div className={cs.buttons}>
        <button className={cs.ok}
          type='button'
          disabled={!isStart}
          onClick={() => {
            //setIsStart(false)
            /* if (stat.settings.sound) {
              clickToMenu()
            } */
            dispatch(attackDown())
            goBattle()
            //setPopUpResult(true)
            setPopUpMove(false)
            //setPopUpResult(true)
            setTimeout(() => {
              setDefaultPopUp((prev) => {
                return { ...prev, active: true }
              })
            }, 200)


          }}
        >{switchLng('attack')}</button>
      </div>}
    </div >
  )
}
