import React, { useState, useEffect } from 'react'

import { useSelector, useDispatch } from 'react-redux';

import { addVolunteers } from '../../../../AI/volunteersAI';

import { allEnemy } from './funcs/funcs';


import { goMoveArmy } from '../../../../store/slices/mainSlice';
import {
  spiritUpValue,
  odUp, odAv, odAvAds,
  raUp, raAv, raAvAds,
  speechUp, speechAv, speechAvAds, speechReset,
  PartMobUp, PartMobAv, PartMobAvAds, PartMobReset,
  downSpecPoints
} from '../../../../store/slices/statSlice';

import { moveUp } from '../../../../store/slices/statSlice';

import ads from '../../../../assets/icons/int_icons/ads+1f1.png'
import close from '../../../../assets/icons/int_icons/close.png'

import cs from './special.module.scss'

export const Special = ({ specialPopUp, setSpecialPopUp, specAttack, setSpecAttack, setInfoPopUp, clickToMenu, switchLng }) => {



  const state = useSelector(state => state.main);
  const stat = useSelector(state => state.stat)
  const dispatch = useDispatch();

  function clickUp(type) {

    if (type === 'od') {

      dispatch(odUp())
      dispatch(odAv())
      dispatch(odAvAds())
      dispatch(moveUp())
      setInfoPopUp((prev) => {
        return {
          ...prev,
          isActive: true,
          data: {
            id: 2,
            title: switchLng('popUpAddMove')
          }
        }
      })
      dispatch(downSpecPoints())

      /* ysdk.adv.showRewardedVideo({
        callbacks: {
          onOpen: () => {
            console.log('Video ad open.');
          },
          onRewarded: () => {
            console.log('Rewarded!');
            dispatch(odUp())
            dispatch(odAv())
            dispatch(odAvAds())
            dispatch(moveUp())
            setInfoPopUp((prev) => {
              return {
                ...prev,
                isActive: true,
                data: {
                  id: 2,
                  title: '+1 '
                }
              }
            })

          },
          onClose: () => {
            console.log('Video ad closed.');
          },
          onError: (e) => {
            console.log('Error while open video ad:', e);
          }
        }
      }) */



      /* dispatch(odUp())
      dispatch(odAv())
      dispatch(odAvAds())
      dispatch(moveUp())
      setInfoPopUp((prev) => {
        return {
          ...prev,
          isActive: true,
          data: {
            id: 2,
            title: '+1 '
          }
        }
      }) */

    }

    if (type === 'ra') {
      if (stat.spec.ra.value === stat.spec.ra.need) {
        console.log('    ')
        allEnemy(state)
        setSpecAttack(true)
        setSpecialPopUp(false)

      } else {
        if (!stat.spec.ra.available) {



          dispatch(raUp())
          dispatch(raAvAds())


          /* ysdk.adv.showRewardedVideo({
            callbacks: {
              onOpen: () => {
                console.log('Video ad open.');
                dispatch(raUp())
                dispatch(raAvAds())
              },
              onRewarded: () => {
                console.log('Rewarded!');
              },
              onClose: () => {
                console.log('Video ad closed.');
              },
              onError: (e) => {
                console.log('Error while open video ad:', e);
              }
            }
          }) */

          return;


        }
        dispatch(raUp())
        dispatch(raAv())
        dispatch(downSpecPoints())
      }
    }

    if (type === 'speech') {
      if (stat.spec.speech.value === stat.spec.speech.need) {
        console.log('')
        const random = Math.round(Math.random() * 2)
        dispatch(spiritUpValue(random))
        setInfoPopUp((prev) => {
          return {
            ...prev,
            isActive: true,
            data: {
              id: 3,
              title: `+ ${random} ${switchLng('popUpAddSpeech')}`
            }
          }
        })
        dispatch(speechReset())
      } else {
        if (!stat.spec.speech.available) {

          dispatch(speechUp())
          dispatch(speechAvAds())


          /* ysdk.adv.showRewardedVideo({
            callbacks: {
              onOpen: () => {
                console.log('Video ad open.');
                dispatch(speechUp())
                dispatch(speechAvAds())
              },
              onRewarded: () => {
                console.log('Rewarded!');
              },
              onClose: () => {
                console.log('Video ad closed.');
              },
              onError: (e) => {
                console.log('Error while open video ad:', e);
              }
            }
          }) */



          return;
        }
        dispatch(speechUp())
        dispatch(speechAv())
        dispatch(downSpecPoints())
      }
    }

    if (type === 'PartMob') {
      if (stat.spec.PartMob.value === stat.spec.PartMob.need) {
        console.log('')

        //console.log(addVolunteers(state, stat, 'mob'))
        const [ruArmys, ukArmys, sumAddRu, sumAddUk] = addVolunteers(state, stat, 'mob');

        ruArmys.map(area => {
          const id = area.id;
          const army = area.army;
          dispatch(goMoveArmy({ id, army }))
        })

        setInfoPopUp((prev) => {
          return {
            ...prev,
            isActive: true,
            data: {
              id: 4,
              title: switchLng('partMobilization'),
              content: sumAddRu
            }
          }
        })
        dispatch(PartMobReset())
      } else {
        if (!stat.spec.PartMob.available) {
          dispatch(downSpecPoints())
          dispatch(PartMobUp())
          dispatch(PartMobAvAds())

          /* ysdk.adv.showRewardedVideo({
            callbacks: {
              onOpen: () => {
                console.log('Video ad open.');
                dispatch(PartMobUp())
                dispatch(PartMobAvAds())
              },
              onRewarded: () => {
                console.log('Rewarded!');
              },
              onClose: () => {
                console.log('Video ad closed.');
              },
              onError: (e) => {
                console.log('Error while open video ad:', e);
              }
            }
          }) */



          return;
        }
        dispatch(PartMobUp())
        dispatch(PartMobAv())
        dispatch(downSpecPoints())
      }
    }
  }

  return (
    <div className={specialPopUp ? cs.popUp : `${cs.popUp_none} ${cs.popUp}`}>

      <div className={cs.buttons}>
        <div className={cs.inf}>
          <p style={{ color: `${stat.specPoints < 1 ? 'lightcoral' : 'lightgreen'}` }}>{stat.specPoints}</p>
        </div>
        <h2 className={cs.title}>{switchLng('special')}</h2>
        <button className={cs.close_modal}
          type='button'
          onClick={() => {
            if (stat.settings.sound) {
              clickToMenu()
            }
            setSpecialPopUp(false)
          }}
        >{/*  */}<img style={{ width: '1.1em', height: '1.1em' }} src={close} alt='close button' /></button>
      </div>

      <div className='table'>

        <div className={cs.firstline}>

          <div className={cs.od_container}>
            <h2 className={cs.title_part}>{switchLng('addMove')}</h2>
            <button className={cs.btn_up} disabled={!stat.spec.od.available && !stat.spec.od.avAds || stat.specPoints < 1}
              style={{ position: "relative" }}
              onClick={() => {
                if (stat.settings.sound) {
                  clickToMenu()
                }
                clickUp('od')
              }}>

              {stat.spec.od.available && stat.spec.od.avAds && <p>+1</p>
                /* < img src={ads}
                  style={{
                    width: 'calc(8vh)',
                    height: 'calc(8vh)', opacity: '0.5'
                  }}></img> */
              }
              {/* {stat.spec.od.available && <p>+1</p>}
 */}
              {!stat.spec.od.available && !stat.spec.od.avAds && <p></p>}
            </button>


            <div className={cs.bar_1}>
              <span className={cs.bar_text}>{stat.spec.od.value}/{stat.spec.od.need}</span>
              <span className={cs.progress} style={{ width: `${stat.spec.od.value / stat.spec.od.need * 100}%` }}>
              </span>
            </div>

          </div>

          {/*   */}

          <div className={cs.ra_container}>
            <h2 className={cs.title_part}>{switchLng('rocketStrike')}</h2>

            <button id='ra' className={cs.btn_up}
              disabled={!stat.spec.ra.available /* && !stat.spec.ra.avAds */ && stat.spec.ra.value !== stat.spec.ra.need || stat.specPoints < 1}
              onClick={() => {
                if (stat.settings.sound) {
                  clickToMenu()
                }
                clickUp('ra')
              }}
            >

              {!stat.spec.ra.available && stat.spec.ra.avAds && stat.spec.ra.value !== stat.spec.ra.need &&
                <p></p>}
              {/* < img src={ads}
                style={{
                  width: 'calc(8vh)',
                  height: 'calc(8vh)', opacity: '0.5'
                }}></img>
 */}
              {stat.spec.ra.available && stat.spec.ra.value !== stat.spec.ra.need && <p>+1</p>}

              {stat.spec.ra.value === stat.spec.ra.need && <div id='ra1' style={{ fontSize: '0.8rem' }}>{switchLng('choose')}</div>}

              {!stat.spec.ra.available && !stat.spec.ra.avAds && <p></p>}
            </button>

            <div className={cs.bar_1}>
              <span className={cs.bar_text}>{stat.spec.ra.value}/{stat.spec.ra.need}</span>
              <span className={cs.progress} style={{ width: `${stat.spec.ra.value / stat.spec.ra.need * 100}%` }}>
              </span>
            </div>
          </div>

        </div>

        <div className={cs.secondline}>

          {/*   */}

          <div className={cs.speech_container}>
            <h2 className={cs.title_part}>{switchLng('addSpeech')}</h2>
            <button className={cs.btn_up}
              disabled={!stat.spec.speech.available /* && !stat.spec.speech.avAds */ && stat.spec.speech.value !== stat.spec.speech.need || stat.specPoints < 1}
              /* style={{ position: "relative" }} */
              onClick={() => {
                if (stat.settings.sound) {
                  clickToMenu()
                }
                clickUp('speech')
              }}>

              {!stat.spec.speech.available && stat.spec.speech.avAds && stat.spec.speech.value !== stat.spec.speech.need &&
                <p></p>}
              {/* < img src={ads}
                style={{
                  width: 'calc(8vh)',
                  height: 'calc(8vh)', opacity: '0.5'
                }}></img> */}

              {stat.spec.speech.available && stat.spec.speech.value !== stat.spec.speech.need && <p>+1</p>}

              {stat.spec.speech.value === stat.spec.speech.need && <div style={{ fontSize: '0.8rem' }}>{switchLng('choose')}</div>}

              {!stat.spec.speech.available && !stat.spec.speech.avAds && stat.spec.speech.value !== stat.spec.speech.need && <p></p>}
            </button>

            <div className={cs.bar_1}>
              <span className={cs.bar_text}>{stat.spec.speech.value}/{stat.spec.speech.need}</span>
              <span className={cs.progress} style={{ width: `${stat.spec.speech.value / stat.spec.speech.need * 100}%` }}>
              </span>
            </div>

          </div>

          {/*   */}

          <div className={cs.mob_container}>
            <h2 className={cs.title_part}>{switchLng('partMobilization')}</h2>
            <button className={cs.btn_up}
              disabled={!stat.spec.PartMob.available /* && !stat.spec.PartMob.avAds */ && stat.spec.PartMob.value !== stat.spec.PartMob.need || stat.specPoints < 1}
              /* style={{ position: "relative" }} */
              onClick={() => {
                if (stat.settings.sound) {
                  clickToMenu()
                }
                clickUp('PartMob')
              }}>

              {!stat.spec.PartMob.available && stat.spec.PartMob.avAds && stat.spec.PartMob.value !== stat.spec.PartMob.need &&
                <p></p>}
              {/* < img src={ads}
                style={{
                  width: 'calc(8vh)',
                  height: 'calc(8vh)', opacity: '0.5'
                }}></img> */}

              {stat.spec.PartMob.available && stat.spec.PartMob.value !== stat.spec.PartMob.need && <p>+1</p>}

              {stat.spec.PartMob.value === stat.spec.PartMob.need && <div style={{ fontSize: '0.8rem' }}>{switchLng('choose')}</div>}

              {!stat.spec.PartMob.available && !stat.spec.PartMob.avAds && stat.spec.PartMob.value !== stat.spec.PartMob.need && <p></p>}
            </button>

            <div className={cs.bar_1}>
              <span className={cs.bar_text}>{stat.spec.PartMob.value}/{stat.spec.PartMob.need}</span>
              <span className={cs.progress} style={{ width: `${stat.spec.PartMob.value / stat.spec.PartMob.need * 100}%` }}>
              </span>
            </div>

          </div>


        </div>


      </div >

    </div >

  )
}
