function battleResult(armies, attackSpirit, defSpirit) {
  console.log(armies)

  const attacker = Object.assign({}, armies[0].army);
  const defender = Object.assign({}, armies[1].army);

  const defenderRatio = Math.random() * (1.5 - 1.2) + 1.3;

  const finallyAttRatio = attackSpirit / 100; // 1 = fighting spirit (100%)
  const finallyDefRatio = defenderRatio * (defSpirit / 100); // 1 = fighting spirit (100%)

  console.log('defender ratio: ', defenderRatio)
  console.log('attacker spirit: ', attackSpirit)
  console.log('defender spirit: ', defSpirit)
  console.log(finallyAttRatio, finallyDefRatio)


  const attackerStrength = {
    foot: attacker.foot,
    tanks: Math.round(attacker.artillery * finallyAttRatio),
    arta: Math.round(attacker.pvo * finallyAttRatio),
    avia: Math.round(attacker.aviation * finallyAttRatio)
  }

  const defenderStrength = {
    foot: Math.round(defender.foot * finallyDefRatio),
    tanks: Math.round(defender.artillery * finallyDefRatio),
    arta: Math.round(defender.pvo * finallyDefRatio),
    avia: Math.round(defender.aviation * finallyDefRatio)
  }

  const sumAttackerStrength = (attackerStrength.foot + attackerStrength.tanks + attackerStrength.arta
    + attackerStrength.avia) * finallyAttRatio;
  const sumDefenderStrength = (defenderStrength.foot + defenderStrength.tanks + defenderStrength.arta
    + defenderStrength.avia) * finallyDefRatio;

  //console.log(finallyAttRatio, finallyDefRatio)
  console.log(sumAttackerStrength, sumDefenderStrength)


  if (sumAttackerStrength > sumDefenderStrength) {
    const remaining = (1 - sumDefenderStrength / sumAttackerStrength)

    console.log(finallyAttRatio)
    console.log(remaining)

    attacker.foot = Math.ceil(attacker.foot * remaining)
    attacker.artillery = Math.ceil(attacker.artillery * remaining)
    attacker.pvo = Math.ceil(attacker.pvo * remaining)
    attacker.aviation = Math.ceil(attacker.aviation * remaining)

    defender.foot = 5
    defender.artillery = 0
    defender.pvo = 0
    defender.aviation = 0

  } else if (sumDefenderStrength > sumAttackerStrength) {
    const remaining = (1 - sumAttackerStrength / sumDefenderStrength)
    console.log(remaining)

    defender.foot = Math.ceil(defender.foot * remaining)
    defender.artillery = Math.ceil(defender.artillery * remaining)
    defender.pvo = Math.ceil(defender.pvo * remaining)
    defender.aviation = Math.ceil(defender.aviation * remaining)

    attacker.foot = 5
    attacker.artillery = 0
    attacker.pvo = 0
    attacker.aviation = 0
  }
  const result = [{ ...armies[0], army: attacker }, { ...armies[1], army: defender }]

  return result;
}

export { battleResult }
