import { permitActions } from '../map/mapLogic/permitActions.js'
import { moveArmy } from './stepMoveAI.js'

function twoMove(state) {
  //  ,      +
  //   -     +
  //  3/4      
  const ru = state.filter((region) => region.country === 'Russia')
  const uk = state.filter((region) => region.country === 'Ukraine')

  const areasForMove = borderNotRussia(state, uk, ru).filter((region) => region.id !== 'UA32')

  console.log(areasForMove)

  //console.log(permitActions[areasForMove[0].id][5] ? true : false)
  if (areasForMove.length < 1) {
    return false;
  }

  const isGo = permitActions[areasForMove[0].id][1]
    ? permitActions[areasForMove[0].id][1]
    : permitActions[areasForMove[0].id][0];

  const areaToMove = uk.filter((region) => (region.id === isGo))//last num is path

  //console.log(areasForMove, areaToMove)
  return moveArmy(areasForMove[0], areaToMove[0], true);

  /* console.log(moveArmy(areasForMove[0], areaToMove[0], true))
  console.log('move prepare', [areasForMove[0], areaToMove[0]]) */

}

function borderNotRussia(state, uk, ru) {
  const areaBorderNotRussia = []

  uk.map((ukArea) => {
    const permit = permitActions[ukArea.id];

    ru.map((ruArea) => {
      if (permit.includes(ruArea.id)) {
        areaBorderNotRussia.push(
          ukArea
        )
      }
    })

  })

  const borderRussia = [...new Set(areaBorderNotRussia)]

  const s = new Set(borderRussia.map(e => JSON.stringify(e)));
  const AreasNotBorderRF = uk.filter(e => !s.has(JSON.stringify(e)))

  const sortedAnbr = AreasNotBorderRF.sort(compareSum)

  function compareSum(a, b) {
    return (
      (b.army.foot + b.army.artillery + b.army.aviation + b.army.pvo) -
      (a.army.foot + a.army.artillery + a.army.aviation + a.army.pvo)
    )
  }

  return sortedAnbr;
}

export { twoMove };
