import React from 'react'
import cs from './MoveModal.module.scss'

import { MoveArmy } from './MoveArmy/MoveArmy'
import { AttackArmy } from './AttackArmy/AttackArmy'

import close from '../../../../assets/icons/int_icons/close.png'

export const MoveModal = ({ setPopUpMove, popUpMove, fromToMove, goAttack, defaultPopUp, setDefaultPopUp, clickToMenu, stat, lng, switchLng }) => {

  return (
    <div className={popUpMove ? cs.popUp : `${cs.popUp_none} ${cs.popUp}`}>
      <div className={cs.title_container}>
        <div className={cs.help}></div>
        <h2 className={cs.title}><span style={{ color: 'lightgreen' }}>{lng === 'en' ? fromToMove[0].nameEn : fromToMove[0].name}</span> {goAttack ? '' : ''} <span style={{ color: `${goAttack ? 'lightcoral' : 'lightgreen'}` }}>{lng === 'en' ? fromToMove[1].nameEn : fromToMove[0].name}</span></h2>
        <div className={cs.close_modal} onClick={() => {
          if (stat.settings.sound) {
            clickToMenu()
          }
          setPopUpMove(false)
        }}>{/*  */} <img style={{ width: '1.1em', height: '1.1em' }} src={close} alt='close button' /> </div>
      </div>
      <div className={cs.modal_container}>

        {goAttack
          ? <AttackArmy fromToMove={fromToMove}
            setPopUpMove={setPopUpMove}
            popUpMove={popUpMove}
            defaultPopUp={defaultPopUp}
            setDefaultPopUp={setDefaultPopUp}
            clickToMenu={clickToMenu}
            switchLng={switchLng}
            lng={lng}
          />
          : <MoveArmy fromToMove={fromToMove}
            setPopUpMove={setPopUpMove}
            popUpMove={popUpMove}
            clickToMenu={clickToMenu}
            switchLng={switchLng}
            lng={lng}
          />
        }

      </div>
    </div>
  )
}
