import React, { useEffect, useState } from 'react'

import { MoveModal } from './Modals/MoveModal.js';
import { DefaultModal } from './Modals/DefaultModal/DefaultModal.js'
import { Endstep } from './END_STEP/Endstep.js';
import { EndTurnPopUP } from './EndTurnModals/EndTurnPopUP.js';
import { EduPopUp } from './Modals/EduPopUp/EduPopUp.js';

import { Special } from './SpecModals/specialModal.js'
import { Extreme } from './ExtremeModals/ExtremeModals.js'

import { InfoPopUp } from './Modals/InfoPopUp/InfoPopUp.js';

import { isActive, sumRuArmy } from './UI_logic/army.js';
import { goTo, clearGotoArea } from './UI_logic/actions.js';

import { rocketAttackFunc } from './UI_logic/rocketAttack.js';
import { stNucAttack } from './UI_logic/StNucAttack.js';

import { isAttack } from './UI_logic/isAttack.js';
import { goAttackArmy, changeArea, swapArmy, rocketAttackD, scoutOpen } from '../../../store/slices/mainSlice.js';
import { raSet, SnReset, scoutGo } from '../../../store/slices/statSlice.js';

import { useSelector, useDispatch } from 'react-redux';

import { ReactComponent as Foot } from './../../../assets/icons/vectorizer/foot(30x30)(1).svg'
import { ReactComponent as Tank } from './../../../assets/icons/vectorizer/tank(30x30).svg'
import { ReactComponent as Arta } from './../../../assets/icons/vectorizer/arta(30x30)(1).svg'
import { ReactComponent as Avia } from './../../../assets/icons/vectorizer/avia(30x30).svg'

import { step } from './../../../AI/stepAI.js'

import backtomenu from '../../../assets/icons/backtomenugray.png'
import education from '../../../assets/icons/educationgray.png'

import cs from './styles.module.scss'

import useSound from 'use-sound';
import rovno from '../../../assets/sound/rovnjajsja_out.mp3'
import attack from '../../../assets/sound/attack.mp3'
import scouting from '../../../assets/sound/scouting.mp3'

export const UI = ({ isActiveUI, setStart, clickToMenu, lng, switchLng }) => {

  const [Rovn] = useSound(rovno)
  const [attackSound] = useSound(attack)
  const [scoutSound] = useSound(scouting)

  const [gotoBtn, setgotoBtn] = useState(false);
  /* const [permitMove, setPermitMove] = useState(true); */
  const [permitArr, setPermitArr] = useState({});

  const [popUpMove, setPopUpMove] = useState(false);
  const [defaultPopUp, setDefaultPopUp] = useState({
    active: false,
    title: '',
    text: '',
    data: [],
  });
  const [endTurnPopUp, setEndTurnPopUp] = useState({
    isActive: false,
    data: [],
  });

  const [infoPopUP, setInfoPopUp] = useState({
    isActive: false,
    data: {
      id: 0
    },
  });

  const [isEduPopUp, setIsEduPopUp] = useState(false);

  const [specialPopUp, setSpecialPopUp] = useState(false);
  const [specAttack, setSpecAttack] = useState(false);

  const [extremePopUp, setExtremePopUp] = useState(false);
  const [isNuclear, setIsNuclear] = useState(false);

  const [sNuclearAttack, setSNuclearAttack] = useState(false);

  const [fromToMove, setFromToMove] = useState([]);
  const [goAttack, setGoAttack] = useState(false);

  const state = useSelector(state => state.main);
  const stat = useSelector(state => state.stat)

  const dispatch = useDispatch();

  const dataRu = () => isActive(state, "Russia")[0] ? isActive(state, "Russia")[0] : sumRuArmy(state, lng).ru;
  const dataUk = () => isActive(state, "Ukraine")[0] ? isActive(state, "Ukraine")[0] : sumRuArmy(state, lng).uk;

  console.log(dataRu().name)

  function gotoMove() {
    setFromToMove([])
    setGoAttack(false)
    if (gotoBtn) {
      setFromToMove([]);
      clearGotoArea()
      setgotoBtn(!gotoBtn)
      //setPermitArr({})
    }
    if (!gotoBtn) {
      goTo(state, dataRu())
      setgotoBtn(!gotoBtn)
      setPermitArr(goTo(state, dataRu()))
      setFromToMove((prev) => [...prev, dataRu()]);
    }
  }

  function resetArea(e) {

    if (e.target.id !== 'btn_move' && e.target.id !== 'btn_move2') {
      //console.log(e.target)
      clearGotoArea()
      setgotoBtn(!gotoBtn)
      //console.log(permitArr)
      if (permitArr !== 'undefined') {
        /* permitArr.forEach(area => {
          console.log(permitArr)
          if (area.id === e.target.id) {
            setPopUpMove(!popUpMove)
            setFromToMove((prev) => [...prev, area])
          }
        }); */

        for (const area of permitArr) {
          if (area.id === e.target.id) {
            setPopUpMove(!popUpMove)
            setFromToMove((prev) => [...prev, area])
            if (stat.settings.sound) {
              Rovn()
            }

          }
        }
      }
      setPermitArr({})
    }
    document.removeEventListener('click', resetArea);
  }

  // ROCKET ATTACK

  function rocketAttack(e) {
    console.log(e.target/* .classList.contains('pulse') *//* ("pulse") */)
    if (e.target.id === 'ra' || e.target.id === 'ra1' || e.target.id === 'ra2' || e.target.id === 'ra3') {
      //document.removeEventListener('click', SNAttack)
      return;
    }
    if (e.target.classList.contains("pulse")) {
      console.log(e.target.id)
      const targetId = e.target.id;
      const [newArm, diffArm, name, nameEn] = rocketAttackFunc(state, targetId, isNuclear)
      console.log(newArm, diffArm, name)
      dispatch(rocketAttackD([newArm, targetId]))
      setSpecAttack(false)
      clearGotoArea()
      setInfoPopUp((prev) => {
        return {
          ...prev,
          isActive: true,
          data: {
            id: 1,
            title: lng === 'en' ? nameEn : name,
            content: diffArm,
          }
        }

      })
      if (!isNuclear) {
        dispatch(raSet())
      } else if (isNuclear) {
        setIsNuclear(false)
      }
      document.removeEventListener('click', rocketAttack)
      console.log('dispatch ok')
    } else if (!e.target.classList.contains("pulse") /* && e.target.id !== 'ra' */) {
      setSpecAttack(false)
      clearGotoArea()
      document.removeEventListener('click', rocketAttack)
      console.log('dispatch ne ok, classlist no')
    }
    document.removeEventListener('click', rocketAttack)
  }

  //  

  function SNAttack(e) {
    console.log(e.target.id)
    if (e.target.id === 'ra4' || e.target.id === 'ra5') {
      //document.removeEventListener('click', SNAttack)
      return;
    }

    if (e.target.classList.contains("pulse")) {

      const targetId = e.target.id;
      const [diffArmy, resultArmy, name, nameEn, diffArmys, resultBorderArmys] = stNucAttack(state, targetId)
      dispatch(rocketAttackD([resultArmy, targetId]))

      document.removeEventListener('click', SNAttack)

      resultBorderArmys.map(area => {
        dispatch(rocketAttackD([area.army, area.id]))
      })

      clearGotoArea()
      setSNuclearAttack(false)

      setInfoPopUp((prev) => {
        return {
          ...prev,
          isActive: true,
          data: {
            id: 7,
            title: switchLng('strategicNuc'),
            content: [[name, nameEn], diffArmy, diffArmys],
          }
        }

      })

      dispatch(SnReset())

    } else if (!e.target.classList.contains("pulse")) {
      clearGotoArea()
      setSNuclearAttack(false)
      document.removeEventListener('click', rocketAttack)
    }

    document.removeEventListener('click', SNAttack)
  }

  if (gotoBtn) {
    document.addEventListener('click', resetArea);
  }

  if (specAttack) {
    console.log('attack ok')
    document.addEventListener('click', rocketAttack);
  }

  if (sNuclearAttack) {
    console.log('SN attack ok')
    document.addEventListener('click', SNAttack);
  }


  console.log(dataUk())

  return (
    <div id="int" className={isActiveUI ? cs.interface : cs.interface_none}>
      <div className={cs.interface_header}>
        <div className={cs.interface_some}>
          <p>
            {switchLng('month')}:  <span style={{ color: "lightgray", fontWeight: "bold" }}>{stat.step.value}</span>
            &nbsp;| {switchLng('movePoint')}: <span style={{ color: stat.step.MovePoint > 0 ? "lightgreen" : "lightcoral" }}>{stat.step.MovePoint}</span>
            &nbsp;| {switchLng('scoutPoint')}: <span style={{ color: stat.step.ScoutPoint > 0 ? "lightgreen" : "lightcoral" }}>{stat.step.ScoutPoint}</span>
            &nbsp;| {switchLng('attackPoint')}: <span style={{ color: stat.step.AttackPoint > 0 ? "lightgreen" : "lightcoral" }}>{stat.step.AttackPoint}</span>
            &nbsp;| {switchLng('spirit')}: <span style={{ color: stat.RuSpirit > 50 ? "lightgreen" : "lightcoral" }}>{stat.RuSpirit}%</span>
          </p>
          <div className={cs.backtomenu}
            onClick={() => {
              if (stat.settings.sound) {
                clickToMenu()
              }
              setStart(false)
            }}
          ><img className={cs.backtomenu_img} src={backtomenu} alt='back to menu' /></div>

          <div className={cs.education}
            onClick={() => {
              if (stat.settings.sound) {
                clickToMenu()
              } setIsEduPopUp(true)
            }}
          ><img className={cs.backtomenu_img} src={education} alt='education' /></div>
        </div>

        <div className={cs.interface_russia}>
          <h2 className={cs.ru_name}>{lng === 'en' ? dataRu().nameEn : dataRu().name}</h2>
          <div className={cs.ru_panel}>
            <div className={cs.ru_stat}>
              <p><Foot /> <span className={cs.army_type}>{dataRu().army.foot}{lng === 'en' ? 'k' : ''}</span> </p>
              <p><Tank /> <span className={cs.army_type}>{dataRu().army.artillery}</span></p>
              <p><Arta /> <span className={cs.army_type}>{dataRu().army.aviation}</span></p>
              <p><Avia /> <span className={cs.army_type}>{dataRu().army.pvo}</span></p>
            </div>
            <div className={cs.controls}>
              <button id='btn_move' className={gotoBtn ? `${cs.btn_go} ${cs.btn_go_active}` : cs.btn_go}
                type="button"
                onClick={() => {
                  if (stat.settings.sound && !gotoBtn) {

                    clickToMenu()
                  }
                  gotoMove()
                }}
                disabled={!((!!isActive(state, "Russia")[0]) && stat.step.MovePoint > 0)}
              >
                {switchLng('march')} ({<span onClick={() => gotoMove()} id='btn_move2' style={{ color: stat.step.MovePoint > 0 ? "lightgreen" : "lightcoral" }}>{stat.step.MovePoint}</span>})
              </button>
            </div>
          </div>

        </div>
      </div>
      <div className={cs.interface_footer}>

        <div className={cs.interface_ukraine}>
          <h2 className={cs.uk_name}>{lng === 'en' ? dataUk().nameEn : dataUk().name}</h2>
          <div className={cs.uk_panel}>
            <div className={cs.ru_stat}>
              <p><Foot /> <span className={cs.army_type}>
                {dataUk().army.scouting ? `${dataUk().army.foot}${lng === 'en' ? 'k' : ''}` : '???'}
              </span></p>
              <p><Tank /> <span className={cs.army_type}>

                {dataUk().army.scouting ? `${dataUk().army.artillery}` : '???'}
              </span></p>
              <p><Arta /> <span className={cs.army_type}>

                {dataUk().army.scouting ? `${dataUk().army.aviation}` : '???'}
              </span></p>
              <p><Avia /> <span className={cs.army_type}>

                {dataUk().army.scouting ? `${dataUk().army.pvo}` : '???'}
              </span></p>
            </div>
            <div className={cs.controls}>
              <button type="button"
                className={cs.btn_attack}
                disabled={((dataRu().id && dataUk().id) ? isAttack(state, dataRu(), dataUk())
                  : true) || stat.step.AttackPoint < 1}

                onClick={() => {
                  //setPopUpMove(!popUpMove)

                  if (stat.settings.sound) {
                    //clickToMenu()
                    attackSound()

                  }
                  setGoAttack(true)
                  setFromToMove([dataRu(), dataUk()])
                  setPopUpMove(true)
                }}
              >{switchLng('attack')}({<span style={{ color: stat.step.AttackPoint > 0 ? "lightgreen" : "lightcoral" }}>{stat.step.AttackPoint}</span>})
              </button>

              <button type="button"
                className={cs.btn_scout}
                disabled={dataUk().id && !dataUk().army.scouting && stat.step.ScoutPoint > 0 ? false : true}

                onClick={() => {
                  //dataUk()
                  if (stat.settings.sound) {
                    scoutSound()
                  }

                  dispatch(scoutGo())
                  dispatch(scoutOpen(dataUk().id))


                  console.log('scout')
                }}

              ><span style={{ fontSize: '0.8em' }}>{switchLng('scouting')}</span>({<span style={{ fontSize: '0.8em', color: stat.step.ScoutPoint > 0 ? "lightgreen" : "lightcoral" }}>{stat.step.ScoutPoint}</span>})
              </button>

              <div style={{ /* marginTop: '1vh' */ fontSize: '0.7em', color: 'lightgray' }}>{switchLng('spirit')}:
                <span style={{ color: stat.UkSpirit > 50 ? "lightcoral" : "lightgreen" }}> {stat.UkSpirit}%</span>
              </div>



            </div>
          </div>

        </div>

        <Endstep
          defaultPopUp={defaultPopUp}
          setDefaultPopUp={setDefaultPopUp}
          setEndTurnPopUp={setEndTurnPopUp}
          setInfoPopUp={setInfoPopUp}
          clickToMenu={clickToMenu}
          lng={lng}
          switchLng={switchLng}

        />

        <div className={cs.interface_russia_add}>
          <button className={cs.spec}
            onClick={() => {
              if (stat.settings.sound) {
                clickToMenu()
              }
              setSpecialPopUp(true)
            }}
          >{switchLng('special')}</button>
          <button className={cs.last}
            onClick={() => {
              if (stat.settings.sound) {
                clickToMenu()
              }
              setExtremePopUp(true)
            }}
          ><span style={{ margin: '0 auto' }}>{switchLng('extreme')}</span></button>
        </div>
      </div>

      {
        specialPopUp && <Special
          specialPopUp={specialPopUp}
          setSpecialPopUp={setSpecialPopUp}
          specAttack={specAttack}
          setSpecAttack={setSpecAttack}
          setInfoPopUp={setInfoPopUp}
          clickToMenu={clickToMenu}
          switchLng={switchLng}
        />
      }

      {
        extremePopUp && <Extreme
          extremePopUp={extremePopUp}
          setExtremePopUp={setExtremePopUp}
          setInfoPopUp={setInfoPopUp}
          setSpecAttack={setSpecAttack}
          setSpecialPopUp={setSpecialPopUp}
          setIsNuclear={setIsNuclear}
          setSNuclearAttack={setSNuclearAttack}
          clickToMenu={clickToMenu}
          switchLng={switchLng}
        />
      }

      {
        fromToMove.length === 2
        && <MoveModal popUpMove={popUpMove}
          setPopUpMove={setPopUpMove}
          goAttack={goAttack}
          setGoAttack={setGoAttack}
          fromToMove={fromToMove.length === 2 ? fromToMove : null}
          defaultPopUp={defaultPopUp}
          setDefaultPopUp={setDefaultPopUp}
          clickToMenu={clickToMenu}
          stat={stat}
          lng={lng}
          switchLng={switchLng}
        />
      }

      {
        defaultPopUp.active && <DefaultModal
          defaultPopUp={defaultPopUp}
          setDefaultPopUp={setDefaultPopUp}
          lng={lng}
          switchLng={switchLng}
        />
      }

      {
        endTurnPopUp.isActive && <EndTurnPopUP
          setEndTurnPopUp={setEndTurnPopUp}
          stat={stat}
          Foot={Foot}
          Tank={Tank}
          Arta={Arta}
          Avia={Avia}
          endTurnPopUp={endTurnPopUp}
          lng={lng}
          switchLng={switchLng}
        />
      }

      {
        infoPopUP.isActive && <InfoPopUp
          infoPopUP={infoPopUP}
          setInfoPopUp={setInfoPopUp}
          setStart={setStart}
          switchLng={switchLng}
          lng={lng}
        />
      }

      {
        isEduPopUp && <EduPopUp
          setIsEduPopUp={setIsEduPopUp}
          clickToMenu={clickToMenu}
          lng={lng}
          switchLng={switchLng}
          stat={stat}
        />
      }
    </div >
  )
}
