import React, { useState } from 'react'

import first from '../../../../../assets/Edu/1.JPG'
import two from '../../../../../assets/Edu/2.JPG'
import three from '../../../../../assets/Edu/4.jpg'
import four from '../../../../../assets/Edu/3.jpg'

import firstEn from '../../../../../assets/Edu/1en.JPG'
import twoEn from '../../../../../assets/Edu/2en.jpg'
import threeEn from '../../../../../assets/Edu/4en.jpg'
import fourEn from '../../../../../assets/Edu/3en.jpg'

import close from '../../../../../assets/icons/int_icons/close.png'

import cs from './EduPopUp.module.scss'

export const EduPopUp = ({ setIsEduPopUp, clickToMenu, stat, lng, switchLng }) => {

  const [slide, setSlide] = useState(1);




  return (
    <div className={cs.popUp}>

      <div className={cs.buttons}>
        <div className={cs.info}>
          
        </div>
        <h2 className={cs.title}>
          <p>
            {switchLng('briefing')}: <span style={{ color: 'lightgreen' }}>{slide === 1 ? switchLng('points')
              : slide === 2 ? switchLng('move')
                : slide === 4 ? switchLng('attack')
                  : slide === 3 ? switchLng('scouting')
                    : slide === 5 ? switchLng('hints')
                      : null}</span>

          </p>
          <p style={{ color: 'lightgreen' }}> ({slide}/5)</p>
        </h2>
        <button className={cs.close_modal}
          type='button'
          onClick={() => {
            if (stat.settings.sound) {
              clickToMenu()
            }
            setIsEduPopUp(false)
          }}
        >{/*  */} <img style={{ width: '1.1em', height: '1.1em' }} src={close} alt='close button' /></button>
      </div>

      {slide === 1 &&
        <div className={cs.slide}>
          <img className={cs.firstimg} src={lng === 'en' ? firstEn : first}></img>
          <div className={cs.content}>
            <p className={cs.row}> <b style={{ color: 'lightgreen' }}>{switchLng('text1')}</b> {switchLng('text2')} <u>{switchLng('text3')}</u></p>
            <p className={cs.row}><b style={{ color: 'lightgreen' }}>{switchLng('text4')}</b> {switchLng('text5')}</p>
            <p className={cs.row}><b style={{ color: 'lightgreen' }}>{switchLng('text6')}</b> {switchLng('text7')}</p>
            <p className={cs.row}><b style={{ color: 'lightgreen' }}>{switchLng('text8')}</b> {switchLng('text9')}</p>
            <p className={cs.row}><b style={{ color: 'lightgreen' }}>{switchLng('text10')}</b> {switchLng('text11')}</p>
          </div>
        </div>
      }

      {slide === 2 &&
        <div className={cs.slide}>
          <img className={cs.firstimg} src={lng === 'en' ? twoEn : two}></img>
        </div>
      }

      {slide === 3 &&
        <div className={cs.slide}>
          <img className={cs.firstimg} src={lng === 'en' ? threeEn : three}></img>
        </div>
      }

      {slide === 4 &&
        <div className={cs.slide}>
          <img className={cs.firstimg} src={lng === 'en' ? fourEn : four}></img>
        </div>
      }

      {slide === 5 &&
        <div className={cs.slide}>
          {/* <img className={cs.firstimg} src={four}></img> */}
          <div className={cs.content}>
            <p className={cs.row}><b style={{ color: 'lightgreen' }}> {switchLng('text12')}</b> {switchLng('text13')}</p>
            <p className={cs.row}><b style={{ color: 'lightgreen' }}> {switchLng('text14')}</b> {switchLng('text15')}</p>
            <p className={cs.row}><b style={{ color: 'lightgreen' }}> {switchLng('text16')}</b> {switchLng('text17')}</p>
            <p className={cs.row}><b style={{ color: 'lightgreen' }}> {switchLng('text18')}</b> {switchLng('text19')}</p>
            <p className={cs.row}><b style={{ color: 'lightgreen' }}></b> {switchLng('text20')} <a target="_blank" href='https://t.me/rukrgame'>@rukrgame</a></p>
          </div>
        </div>
      }

      <button
        className={cs.btn_ok}
        disabled={slide < 2}
        onClick={() => setSlide(prev => prev - 1)}
      >{switchLng('back')}</button>

      <button
        className={cs.btn_ok}
        disabled={slide > 4}
        onClick={() => setSlide(prev => prev + 1)}
      >{switchLng('next')}</button>

    </div>
  )
}
