
export const isActive = (state, cntr) => {
  const all = state.filter((region) => region.country === cntr)
  return all.filter(oneState => oneState.active === true)
}

export const sumRuArmy = (state, lng) => {

  const ru = state.filter((region) => region.country === 'Russia')
  const uk = state.filter((region) => region.country === 'Ukraine')

  const obj = {
    ru: {
      name: ' ',
      nameEn: 'Russian Army',
      army: {
        foot: ru.reduce((acc, oneState) => acc += oneState.army.foot, 0),
        artillery: ru.reduce((acc, oneState) => acc += oneState.army.artillery, 0),
        aviation: ru.reduce((acc, oneState) => acc += oneState.army.aviation, 0),
        pvo: ru.reduce((acc, oneState) => acc += oneState.army.pvo, 0),
      }
    },
    uk: {
      name: ' ',
      nameEn: 'Ukrainian Army',
      army: {
        foot: uk.reduce((acc, oneState) => acc += oneState.army.foot, 0),
        artillery: uk.reduce((acc, oneState) => acc += oneState.army.artillery, 0),
        aviation: uk.reduce((acc, oneState) => acc += oneState.army.aviation, 0),
        pvo: uk.reduce((acc, oneState) => acc += oneState.army.pvo, 0),
      },
    }
  };
  return obj;
}
