import { permitActions } from '../../../../map/mapLogic/permitActions'

function stNucAttack(state, id) {

  const multiplyArea = 1.2
  const multiplyBorders = 1.6

  const area = state.filter(area => area.id === id)[0]
  const borderAreasId = permitActions[id]
  const borderAreas = [];

  state.map(area => {
    if (borderAreasId.includes(area.id)) {
      borderAreas.push(area)
    }
  })

  const diffArmy = {
    foot: Math.round(area.army.foot / multiplyArea),
    artillery: Math.round(area.army.artillery / multiplyArea),
    aviation: Math.round(area.army.aviation / multiplyArea),
    pvo: Math.round(area.army.pvo / multiplyArea)
  }

  const diffArmys = []

  const resultArmy = {
    foot: Math.round(area.army.foot - diffArmy.foot),
    artillery: Math.round(area.army.artillery - diffArmy.artillery),
    aviation: Math.round(area.army.aviation - diffArmy.aviation),
    pvo: Math.round(area.army.pvo - diffArmy.pvo)
  }

  const resultBorderArmys = borderAreas.map(area => {
    diffArmys.push({
      id: area.id,
      name: area.name,
      nameEn: area.nameEn,
      army: {
        foot: Math.round(area.army.foot / multiplyBorders),
        artillery: Math.round(area.army.artillery / multiplyBorders),
        aviation: Math.round(area.army.aviation / multiplyBorders),
        pvo: Math.round(area.army.pvo / multiplyBorders),
      }
    })
    return {
      ...area,
      army: {
        ...area.army,
        foot: Math.round(area.army.foot - (area.army.foot / multiplyBorders)),
        artillery: Math.round(area.army.artillery - (area.army.artillery / multiplyBorders)),
        aviation: Math.round(area.army.aviation - (area.army.aviation / multiplyBorders)),
        pvo: Math.round(area.army.pvo - (area.army.pvo / multiplyBorders)),
      }
    }
  })

  return [diffArmy, resultArmy, area.name, area.nameEn, diffArmys, resultBorderArmys]

}

export { stNucAttack };
