import { isActive } from "./army"
import { permitActions } from '../../../../map/mapLogic/permitActions';

function isAttack(state, dataRu, dataUk) {

  const isCheck = () => !isActive(state, "Ukraine")[0] > 0 //   ?

  const permit = permitActions[dataRu.id];
  const allUk = state.filter((area) => area.country === 'Ukraine')

  const finalPermit = allUk.filter((area) => permit.includes(area.id))
  const finalPermit2 = finalPermit.filter(area => area.id === dataUk.id)

  if (isCheck() === false && finalPermit2.length === 0) {
    return true;
  } else {
    return false;
  }
}


export { isAttack };
