function lossesArmys(attackCity, loseCity) {

  console.log(attackCity)
  console.log(loseCity)

  console.log((attackCity[0].army.artillery + attackCity[0].army.aviation +
    attackCity[0].army.pvo) - (attackCity[1].army.artillery + attackCity[1].army.aviation +
      attackCity[1].army.pvo))


  const differenceAttackerArmy = {
    ...attackCity[0],
    army: {
      ...attackCity[0].army
    },
    foot: attackCity[0].army.foot - attackCity[1].army.foot,
    technic: (attackCity[0].army.artillery + attackCity[0].army.aviation +
      attackCity[0].army.pvo) - (attackCity[1].army.artillery + attackCity[1].army.aviation +
        attackCity[1].army.pvo)
  }

  const differenceLoseArmy = {
    ...loseCity[0],
    army: {
      ...loseCity[0].army
    },
    foot: loseCity[0].army.foot - loseCity[1].army.foot,
    technic: (loseCity[0].army.artillery + loseCity[0].army.aviation +
      loseCity[0].army.pvo) - (loseCity[1].army.artillery + loseCity[1].army.aviation +
        loseCity[1].army.pvo)
  }

  console.log(differenceAttackerArmy, differenceLoseArmy)

  return [differenceAttackerArmy, differenceLoseArmy]

}

export { lossesArmys }

