import { permitActions } from '../map/mapLogic/permitActions.js'
import { battleResult } from './battle.js'


function step(state, stat) {
  const ru = state.filter((region) => region.country === 'Russia')
  const uk = state.filter((region) => region.country === 'Ukraine')

  console.log(ru, uk)

  const forAttak = borderingArea(uk, ru, state)
  let acceptAttak = [[], []];

  console.log(forAttak)

  forAttak.map((twoArea) => {

    const [attacker, defer] = battleResult(twoArea, stat.UkSpirit, stat.RuSpirit);

    console.log(attacker, defer)

    const sumAttacker = attacker.army.foot
      + attacker.army.artillery
      + attacker.army.aviation
      + attacker.army.pvo;


    console.log(sumAttacker)

    if (sumAttacker > 5) {
      acceptAttak[0] = twoArea;
      acceptAttak[1] = [attacker, defer];
    }

  })

  return acceptAttak;
}


function borderingArea(uk, ru, state) {
  const isAttack = [];

  uk.map((ukArea) => {
    const permit = permitActions[ukArea.id];

    ru.map((ruArea) => {
      if (permit.includes(ruArea.id)) {
        isAttack.push(
          [
            state.find(area => area.id === ukArea.id),
            state.find(area => area.id === ruArea.id)
          ]
        )
      }
    })
  })

  return isAttack;
}

export { step, borderingArea };
