import { permitActions } from '../../../../map/mapLogic/permitActions';

function goTo(state, dataRu) {

  const permit = permitActions[dataRu.id];
  const allRu = state.filter((area) => area.country === 'Russia')

  const finalPermit = allRu.filter((area) => permit.includes(area.id))

  for (const { id } of finalPermit) {
    document.getElementById(`${id}`).classList.add('pulse')
  }
  console.log(finalPermit)
  return finalPermit;
}

function clearGotoArea() {
  const allLand = document.querySelectorAll('.land')
  /* allLand.forEach(element => {
    element.classList.remove('pulse')
  }); */
  for (const el of allLand) {
    el.classList.remove('pulse')
  }
}

export { goTo, clearGotoArea };


