function rocketAttackFunc(state, id, isNuclear) {
  const area = state.filter(area => area.id === id)[0]
  console.log(area)

  const nuclear = isNuclear ? 2 : 10;

  const diffArmy = {
    foot: Math.round(area.army.foot / nuclear),
    artillery: Math.round(area.army.artillery / nuclear),
    aviation: Math.round(area.army.aviation / nuclear),
    pvo: Math.round(area.army.pvo / nuclear)
  }

  const newArmy = {
    foot: Math.round(area.army.foot - diffArmy.foot),
    artillery: Math.round(area.army.artillery - diffArmy.artillery),
    aviation: Math.round(area.army.aviation - diffArmy.aviation),
    pvo: Math.round(area.army.pvo - diffArmy.pvo)
  }

  return [newArmy, diffArmy, area.name, area.nameEn]

}

export { rocketAttackFunc };
