import { createSlice } from "@reduxjs/toolkit"
import { statdata } from '../../data/statdata.js'

export const statSlice = createSlice({
  name: 'statSlice',
  initialState: JSON.parse(window.localStorage.getItem('rukrStateStat02')) || statdata,
  reducers: {
    test_0: () => {
      console.log('test')
    },
    soundToggle: (state) => {
      state.settings.sound = !state.settings.sound;
    },
    musicToggle: (state) => {
      state.settings.music = !state.settings.music;
    },
    musicOff: (state) => {
      state.settings.music = false;
    },
    musicOn: (state) => {
      state.settings.music = true;
    },
    resetSState: (state) => {
      const { settings } = state;
      return state = {
        ...statdata,
        settings
      }
    },
    setStat: (state) => {
      return {
        ...state,
        step: {
          ...state.step,
          "MovePoint": 2,
          "ScoutPoint": 2,
          "AttackPoint": 1,
        },
        spec: {
          ...state.spec,
          od: {
            ...state.spec.od,
            "value": 0,
            "available": true,
            "avAds": true,
          },
          ra: {
            ...state.spec.ra,
            "available": true,
            "avAds": true,
          },
          speech: {
            ...state.spec.speech,
            "available": true,
            "avAds": true,
          },
          PartMob: {
            ...state.spec.PartMob,
            "available": true,
            "avAds": true,
          },
        },
        extreme: {
          ...state.extreme,
          WagnerHelp: {
            ...state.extreme.WagnerHelp,
            "avAds": true,
          },
          FullMob: {
            ...state.extreme.FullMob,
            "avAds": true,
          },
          TacticNuclear: {
            ...state.extreme.TacticNuclear,
            "avAds": true,
          },
          StrategicNuclear: {
            ...state.extreme.StrategicNuclear,
            "avAds": true,
          },

        }
      }

    },
    monthUp: (state) => {
      state.step.value += 1;
    },
    moveUp: (state) => {
      state.step.MovePoint += 1;
    },
    moveDown: (state) => {
      state.step.MovePoint -= 1;
    },
    scoutGo: (state) => {
      state.step.ScoutPoint -= 1;
    },
    attackDown: (state) => {
      state.step.AttackPoint -= 1;
    },
    spiritUp: (state) => {
      state.RuSpirit += 1;
    },
    spiritUpValue: (state, action) => {
      state.RuSpirit += action.payload;
    },
    spiritDown: (state) => {
      state.RuSpirit -= 1;
    },
    spiritEnemyUp: (state) => {
      state.UkSpirit += 1;
    },
    spiritEnemyDown: (state) => {
      state.UkSpirit -= 1;
    },
    //===
    odUp: (state) => {
      state.spec.od.value += 1;
    },
    odAv: (state) => {
      state.spec.od.available = !state.spec.od.available;
    },

    odAvAds: (state) => {
      state.spec.od.avAds = !state.spec.od.avAds;
    },
    raUp: (state) => {
      state.spec.ra.value += 1;
    },
    raAv: (state) => {
      state.spec.ra.available = !state.spec.ra.available;
    },
    raAvAds: (state) => {
      state.spec.ra.avAds = !state.spec.ra.avAds;
    },
    raSet: (state) => {
      state.spec.ra.value = 0;
      state.spec.ra.available = false;
      state.spec.ra.avAds = false;
    },
    speechUp: (state) => {
      state.spec.speech.value += 1;
    },
    speechAv: (state) => {
      state.spec.speech.available = !state.spec.speech.available;
    },
    speechAvAds: (state) => {
      state.spec.speech.avAds = !state.spec.speech.avAds;
    },
    speechReset: (state) => {
      state.spec.speech.value = 0;
      state.spec.speech.available = false;
      state.spec.speech.avAds = false;
    },
    PartMobUp: (state) => {
      state.spec.PartMob.value += 1;
    },
    PartMobAv: (state) => {
      state.spec.PartMob.available = !state.spec.PartMob.available;
    },
    PartMobAvAds: (state) => {
      state.spec.PartMob.avAds = !state.spec.PartMob.avAds;
    },
    PartMobReset: (state) => {
      state.spec.PartMob.value = 0;
      state.spec.PartMob.available = false;
      state.spec.PartMob.avAds = false;
    },
    WagnerUp: (state) => {
      state.extreme.WagnerHelp.value += 1;
    },
    WagnerAvAds: (state) => {
      state.extreme.WagnerHelp.avAds = !state.extreme.WagnerHelp.avAds;
    },
    WagnerReset: (state) => {
      state.extreme.WagnerHelp.value = 0;
      state.extreme.WagnerHelp.avAds = false;
    },

    FullMobUp: (state) => {
      state.extreme.FullMob.value += 1;
    },
    FullMobAvAds: (state) => {
      state.extreme.FullMob.avAds = !state.extreme.FullMob.avAds;
    },
    FullMobReset: (state) => {
      state.extreme.FullMob.value = 0;
      state.extreme.FullMob.avAds = false;
    },

    TnUp: (state) => {
      state.extreme.TacticNuclear.value += 1;
    },
    TnAvAds: (state) => {
      state.extreme.TacticNuclear.avAds = !state.extreme.TacticNuclear.avAds;
    },
    TnReset: (state) => {
      state.extreme.TacticNuclear.value = 0;
      state.extreme.TacticNuclear.avAds = false;
    },

    SnUp: (state) => {
      state.extreme.StrategicNuclear.value += 1;
    },
    SnAvAds: (state) => {
      state.extreme.StrategicNuclear.avAds = !state.extreme.StrategicNuclear.avAds;
    },
    SnReset: (state) => {
      state.extreme.StrategicNuclear.value = 0;
      state.extreme.StrategicNuclear.avAds = false;
    },
    downSpecPoints: (state) => {
      state.specPoints -= 1;
    },
    setSpecPoints: (state) => {
      state.specPoints = Math.floor(Math.random() * 3) + 3;
    },

  }
})


export const { test_0, soundToggle, musicToggle, musicOff, musicOn, resetSState, newStatState, setStat, monthUp, moveUp, moveDown, scoutGo, attackDown,
  spiritUp, spiritUpValue, spiritDown, spiritEnemyUp, spiritEnemyDown, downSpecPoints, setSpecPoints } = statSlice.actions;
//spec
export const {
  odUp, odAv, odAvAds,
  raUp, raAv, raAvAds, raSet,
  speechUp, speechAv, speechAvAds, speechReset,
  PartMobUp, PartMobAv, PartMobAvAds, PartMobReset
} = statSlice.actions;
//extreme
export const {
  WagnerUp, WagnerAvAds, WagnerReset,
  FullMobUp, FullMobAvAds, FullMobReset,
  TnUp, TnAvAds, TnReset,
  SnUp, SnAvAds, SnReset
} = statSlice.actions;

export default statSlice.reducer;
