import { useState, useEffect } from 'react';
import { useSelector, useDispatch } from "react-redux";
import { useSound } from 'use-sound'
//import { usePageLeave } from 'react-use'

import { MainMenu } from './components/menu/MainMenu.js';
import { UI } from './components/menu/UI/UI.js';

import { colorLands, colorActiveLand, differenColors } from './map/mapLogic/index.js'
import { activeLand } from './store/slices/mainSlice.js';
import { musicOff, musicOn } from './store/slices/statSlice.js'

import Map from './map/index.js';
import cs from './styles.module.scss';

import music_menu from './assets/music/main_menu.mp3'
//import music_game from './assets/music/game-music.mp3'
import clickMenu from './assets/sound/click_menu.mp3'

//import fire from './assets/gifs/fire.gif'

import { lang } from './data/lang.js'

import bridge from '@vkontakte/vk-bridge';



document.addEventListener('contextmenu', function (e) {
  e.preventDefault();
});

bridge.send("VKWebAppInit", {});


export default function App() {

  const [start, setStart] = useState(false);
  const [lng, setLng] = useState('ru');

  function switchLng(str) {
    return lang[lng][str]
  }


  const [musicMenu, { stop: stop, pause: pauseMusicMenu }] = useSound(music_menu, { volume: 0.3, loop: true });
  const [isPlay, setIsPlay] = useState(false);
  const [musicClick, setMusicClick] = useState(false);
  //const [game_music, { stop: stopGameMusic }] = useSound(music_game, { volume: 0.6, loop: true });
  const [clickToMenu] = useSound(clickMenu, { volume: 0.3 })

  //const isVisible = usePageLeave('visibility');


  const states = useSelector(state => state.main);
  const stat = useSelector(state => state.stat)
  const dispatch = useDispatch();

  useEffect(() => {
    colorLands(states)
    differenColors(states)
    colorActiveLand(states)

  }, [states])

  /*  useEffect(() => {
     YaGames
       .init()
       .then(ysdk => {
         console.log('Yandex SDK initialized');
         window.ysdk = ysdk;
       });
   }, []) */

  useEffect(() => {
    if (!start && stat.settings.music) {
      musicMenu();

      setIsPlay(true)
    }
    return stop;

  });

  useEffect(() => {
    if (musicClick) {
      const mouseEnter = () => {
        if (!stat.settings.music && !isPlay) {
          dispatch(musicOn())
          setIsPlay(true);
        }
      }

      const mouseLeave = () => {
        if (stat.settings.music && isPlay) {
          dispatch(musicOff())
          setIsPlay(false);
        }
      }

      document.addEventListener('mouseleave', mouseLeave);
      document.addEventListener('mouseenter', mouseEnter);

      return () => {
        document.removeEventListener('mouseenter', mouseEnter);
        document.removeEventListener('mouseleave', mouseLeave);
      }
    }

  })

  console.log('isPlay: ', isPlay)
  console.log('noUseState: ', lng)

  // capital city names
  useEffect(() => {
    if (lng === 'en') {
      if (document.querySelector('.capitalru') === null && document.querySelector('.capitaluk') === null) {
        window.onload = () => {

          document.querySelector('.capitalru').textContent = 'Sevastopol'
          document.querySelector('.capitaluk').textContent = 'Kyiv'
        }
      } else {
        document.querySelector('.capitalru').textContent = 'Sevastopol'
        document.querySelector('.capitaluk').textContent = 'Kyiv'
      }
    } if (lng === 'ru') {
      document.querySelector('.capitalru').textContent = ''
      document.querySelector('.capitaluk').textContent = ''
    }
  }, [lng])


  return (
    <>
      <h1 className={cs.title_vert}>{switchLng('rotate')}</h1>
      <div className={cs.app}>
        <h1 className={start ? cs.title_none : cs.title}>{switchLng('title')}</h1 >

        <MainMenu
          start={start}
          setStart={setStart}
          clickToMenu={clickToMenu}
          setMusicClick={setMusicClick}
          musicMenu={musicMenu}
          lng={lng}
          setLng={setLng}
          switchLng={switchLng}
        />

        <div className={`${cs.map_container} ${start ? cs.map_container_flex : null}`} >
          <Map
            className={start ? cs.map_active : cs.map}
            onClick={function (e) {
              dispatch(activeLand(e.target.id))
              console.log(e.target.id)
            }}
          />

          <UI
            isActiveUI={start}
            setStart={setStart}
            clickToMenu={clickToMenu}
            lng={lng}
            setLng={setLng}
            switchLng={switchLng}
          />

        </div>
      </div >
    </>

  );
}
