import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux';

import { step } from '../../../../AI/stepAI';
import { moveAI } from '../../../../AI/stepMoveAI';
import { twoMove } from '../../../../AI/twoMoveAI';
import { threeMoveAi } from '../../../../AI/threeMoveAi';
import { addVolunteers } from '../../../../AI/volunteersAI';

import { isVictory } from './isWin';

import { goAttackArmy, changeArea, swapArmy, goMoveArmy, stage2Ru, stage2Uk, setScout, lng, switchLng } from '../../../../store/slices/mainSlice.js';
import { setStat, monthUp, setSpecPoints, spiritEnemyUp, spiritDown } from '../../../../store/slices/statSlice.js';
import { lossesArmys } from './armlosses.js'

import { stage2RuAreas, stage2UkAreas } from '../../../../map/mapLogic/stage2Ru';

import useSound from 'use-sound';
import EndSound from '../../../../assets/sound/end_turn1.mp3'

import cs from './endstep.module.scss'




export const Endstep = ({ defaulPopUp, setDefaultPopUp, setEndTurnPopUp, setInfoPopUp, clickToMenu, lng, switchLng }) => {

  const [endplay] = useSound(EndSound);

  const [accesTwoStep, setAccesTwoStep] = useState(false);
  const [accesThreeStep, setAccesThreeStep] = useState(false);

  //for modal window
  const [attackCity, setAttackCity] = useState([]);
  const [loseCity, setLoseCity] = useState([]);

  const [btnDisable, setBtnDisable] = useState(false)

  const [stage2Start, setStage2Start] = useState(false)

  const state = useSelector(state => state.main);
  const stat = useSelector(state => state.stat)

  const dispatch = useDispatch();

  function endStep() {

    //ysdk.adv.showFullscreenAdv()

    btnDisabledFor1sec()
    //attack
    const twoArea = step(state, stat);
    const [sourceAreas, resultAreas] = twoArea;

    if (resultAreas.length !== 0) {
      setAttackCity([sourceAreas[0]])
      setLoseCity([sourceAreas[1]])

      resultAreas.map(area => {
        const id = area.id;
        const army = area.army;
        dispatch(goAttackArmy({ id, army }));

      })

      dispatch(changeArea(resultAreas[1].id))
      dispatch(swapArmy([resultAreas[0].id, resultAreas[1].id]))

      setAttackCity((prev) => [...prev, resultAreas[0]])
      setLoseCity((prev) => [...prev, resultAreas[1]])

      //moveOne
      //console.log(moveAI(sourceAreas[0], state))
      if (moveAI(sourceAreas[0], state) !== undefined) {
        moveAI(sourceAreas[0], state).map((area) => {
          dispatch(goMoveArmy(area))
        })
      }

    }

    //console.log(attackCity, loseCity)

    setAccesTwoStep(true)

  }

  //twoMove
  function endStepMoves() {
    const forMove = twoMove(state) /* ? twoMove(state) : false */;
    if (!forMove) {
      setAccesTwoStep(false)
      setAccesThreeStep(true)
      return;
    }
    //console.log(forMove)
    forMove.map((area) => {
      dispatch(goMoveArmy(area))
    })
    setAccesTwoStep(false)
    setAccesThreeStep(true)
  }

  //threeMove
  function threeMove() {
    //console.log('three move')
    threeMoveAi(state)
    dispatch(monthUp())
    dispatch(setStat())
    dispatch(setSpecPoints())

    //console.log('restate')

    //console.log(addVolunteers(state, stat))

    const [ruArmys, ukArmys, sumAddRu, sumAddUk] = addVolunteers(state, stat);

    ukArmys.map(area => {
      const id = area.id;
      const army = area.army;
      dispatch(goMoveArmy({ id, army }))
    })

    ruArmys.map(area => {
      const id = area.id;
      const army = area.army;
      dispatch(goMoveArmy({ id, army }))
    })

    setEndTurnPopUp((prev) => {
      return {
        ...prev,
        isActive: true,
        data: [sumAddRu, sumAddUk],
      }
    })

    //2 stage game
    //console.log(state[11])
    if (state[11].country === 'Russia' && !stage2Start) {
      //console.log(state[11])

      stage2RuAreas.map(areaId => {
        dispatch(stage2Ru(areaId))
      })

      stage2UkAreas.map(areaId => {
        //console.log(areaId)
        dispatch(stage2Uk(areaId))
      })

      setInfoPopUp(prev => {
        return {
          ...prev,
          isActive: true,
          data: {
            id: 8,
            title: switchLng('polishTitle'),
            content: switchLng('polishText'),
          }
        }
      })

      setStage2Start(true)

    }

    //VICTORY
    if (isVictory(state)) {
      setInfoPopUp(prev => {
        return {
          ...prev,
          isActive: true,
          data: {
            id: 9,
            title: switchLng('victory'),
            content: switchLng('victoryText')
          }
        }
      })
    }

    //GAMEOVER
    if (state[0].country === 'Ukraine') {
      setInfoPopUp(prev => {
        return {
          ...prev,
          isActive: true,
          data: {
            id: 10,
            title: switchLng('defeat'),
            content: switchLng('defeatText')
          }
        }
      })
    }

    if (stat.step.value % 5 === 0) {
      dispatch(setScout())
    }


    localStorage.setItem("rukrStateMain01", JSON.stringify(state))
    localStorage.setItem("rukrStateStat02", JSON.stringify(stat))

    setAccesThreeStep(false)
  }

  //1s btnDisabled
  function btnDisabledFor1sec() {
    setBtnDisable(true)
    setTimeout(() => {
      setBtnDisable(false);
    }, 500);
  }

  //console.log(accesTwoStep, accesThreeStep);

  useEffect(() => {
    if (accesTwoStep) {
      endStepMoves()
    }
    if (accesThreeStep) {
      threeMove()
    }
  }, [accesTwoStep])

  //popUp difference after enemy attack
  useEffect(() => {
    if (loseCity.length > 0) {
      const difference = lossesArmys(attackCity, loseCity)

      console.log(difference)

      setDefaultPopUp(() => {
        return {
          type: 'enemy',
          active: true,
          text: 'test2',
          isWin: false,
          areas: [lng === 'en' ? difference[0].nameEn : difference[0].name, lng === 'en' ? difference[1].nameEn : difference[1].name],
          title: `${switchLng('lost')}: ${lng === 'en' ? difference[1].nameEn : difference[1].name}`,
          data: difference,
          btn: "!"
        }
      })

      //dispatch(spiritEnemyUp())
    }
  }, [loseCity])



  //console.log('losecity', loseCity, 'length: ', loseCity.length)
  //console.log('attackCity', attackCity, 'length: ', attackCity.length)

  return (
    <div className={cs.endstep_container}>
      <button
        onClick={() => {
          if (stat.settings.sound) {
            //clickToMenu()
            endplay()
          }
          endStep()
        }}
        disabled={btnDisable}
        className={cs.endstep}>{switchLng('endTurn')}</button>
    </div>

  )
}
