import React, { useState } from 'react'
import useSound from 'use-sound';
import { useDispatch, useSelector } from 'react-redux';
import { goMoveArmy } from '../../../../../store/slices/mainSlice';
import { moveDown } from '../../../../../store/slices/statSlice'

import marsh from '../../../../../assets/sound/marsh_out.mp3'

import cs from './MoveArmy.module.scss'

export const MoveArmy = ({ fromToMove, setPopUpMove, PopUpMove, clickToMenu, switchLng, lng }) => {

  const [goMarsh] = useSound(marsh)

  const [newData, setNewData] = useState(fromToMove);

  const stat = useSelector(state => state.stat)
  const dispatch = useDispatch();

  const max = (type) => fromToMove[0].army[type] + fromToMove[1].army[type]

  const changeData = (event, type) => {
    const newValue = parseInt(event.target.value);
    setNewData((prevData) => ({
      ...prevData,
      1: {
        ...prevData[1],
        army: {
          ...prevData[1].army,
          [type]: newValue,
        },
      },
      0: {
        ...prevData[0],
        army: {
          ...prevData[0].army,
          [type]: max(type) - newValue,
        },
      },
    }));
  }

  const goArmy = (data) => {
    const newData = [data[0], data[1]];

    newData.map(area => {
      const id = area.id;
      const army = area.army;
      dispatch(goMoveArmy({ id, army }))
    })

    setPopUpMove(false)

    dispatch(moveDown())
  }

  return (
    <div className={cs.main_container}>
      <div className={cs.from}>
        <div className={cs.line_army}>
          <h2 className={cs.name_army}>{switchLng('foot')}</h2>
          <p className={cs.values}>{newData[0].army.foot}{lng === 'en' ? 'k' : '.'}</p>

          <input id='foot'
            type='range'
            value={newData[1].army.foot}
            max={max('foot') - 1}
            min={1}
            onChange={(event) => changeData(event, 'foot')}
          ></input>

          <p className={cs.values}>{newData[1].army.foot} {lng === 'en' ? 'k' : '.'}</p>
        </div>
        <div>
          <h2 className={cs.name_army}>{switchLng('tanks')}</h2>
          <p className={cs.values}>{newData[0].army.artillery}</p>

          <input id='artillery'
            type='range'
            value={newData[1].army.artillery}
            max={max('artillery')}
            min={0}
            onChange={(event) => changeData(event, 'artillery')}
          ></input>

          <p className={cs.values}>{newData[1].army.artillery}</p>
        </div>

        <div>
          <h2 className={cs.name_army}>{switchLng('artillery')}</h2>
          <p className={cs.values}>{newData[0].army.pvo}</p>

          <input id='pvo'
            type='range'
            value={newData[1].army.pvo}
            max={max('pvo')}
            min={0}
            onChange={(event) => changeData(event, 'pvo')}
          ></input>

          <p className={cs.values}>{newData[1].army.pvo}</p>
        </div>

        <div>
          <h2 className={cs.name_army}>{switchLng('aviation')}</h2>
          <p className={cs.values}>{newData[0].army.aviation}</p>

          <input id='aviation'
            type='range'
            value={newData[1].army.aviation}
            max={max('aviation')}
            min={0}
            onChange={(event) => changeData(event, 'aviation')}
          ></input>

          <p className={cs.values}>{newData[1].army.aviation}</p>
        </div>



        <div className={cs.buttons}>
          <button className={cs.ok}
            type='button'
            onClick={() => {
              if (stat.settings.sound) {
                //clickToMenu()
                //console.log(stat.settings.sound)
                goMarsh()
              }
              goArmy(newData)
            }}
          >{/*  */} {switchLng('march')}</button>
        </div>

      </div>

    </div >
  )
}
