
import { sumRuArmy } from "../../components/menu/UI/UI_logic/army"

export const colorLands = (states) => {

  states.map((stateOne) => {
    if (stateOne.country === 'Russia') {
      document.querySelector(`#${stateOne.id}`)
        .classList.remove('color-Ukraine')
      document.querySelector(`#${stateOne.id}`)
        .classList.add('color-Russia')
    } else {
      document.querySelector(`#${stateOne.id}`)
        .classList.add('color-Ukraine')
    }
  })

}

export const colorActiveLand = (states) => {
  states.map((stateOne) => {
    if (stateOne.active === true) {
      document.querySelector(`#${stateOne.id}`)
        .classList.add('active')
    }
    if (stateOne.active === false) {
      document.querySelector(`#${stateOne.id}`)
        .classList.remove('active')
    }
  })
}

export const differenColors = (states) => {

  const obj = sumRuArmy(states)

  const sumUk = obj.uk.army.foot + obj.uk.army.artillery + obj.uk.army.aviation + obj.uk.army.pvo
  const sumRu = obj.ru.army.foot + obj.ru.army.artillery + obj.ru.army.aviation + obj.ru.army.pvo

  states.map((area) => {
    if (area.country === 'Russia') {
      const sumArea = area.army.foot + area.army.artillery + area.army.aviation + area.army.pvo
      document.querySelector(`#${area.id}`).style.fill = `hsl(180, 20%, ${50 - (sumArea / sumRu * 100)}%)`;

      //console.log(50 - (sumArea / sumRu * 100))
    } else if (area.country === 'Ukraine') {
      const sumArea = area.army.foot + area.army.artillery + area.army.aviation + area.army.pvo
      document.querySelector(`#${area.id}`).style.fill = `hsl(30, 50%, ${40 - (sumArea / sumUk * 100)}%)`;
    }
  })

}
