import React from 'react'
import { useSelector } from 'react-redux';



import cs from './endturnpopup.module.scss'

export const EndTurnPopUP = ({ setEndTurnPopUp, stat, Foot, Tank, Arta, Avia, endTurnPopUp, lng, switchLng }) => {

  //const stat = useSelector(state => state.stat)

  //console.log(endTurnPopUp.data)

  return (
    <div className={cs.defmodal}>
      <h2>{switchLng('volunteersfor')} {stat.step.value - 1} {switchLng('month')}</h2>
      <div className={cs.table}>
        <p className={cs.row}><Foot /><span className={cs.army_type}> + {endTurnPopUp.data[0][0]}{lng === 'en' ? 'k' : '.'}</span></p>
        <p><Tank /><span className={cs.army_type}> + {endTurnPopUp.data[0][1]}</span></p>
        <p><Arta /><span className={cs.army_type}> + {endTurnPopUp.data[0][2]}</span></p>
        <p><Avia /><span className={cs.army_type}> + {endTurnPopUp.data[0][3]}</span></p>
      </div>

      <button className={cs.btn_ok}
        onClick={() => setEndTurnPopUp(false)}
      >
        {switchLng('good')}

      </button>
    </div>

  )
}
