import { permitActions } from '../map/mapLogic/permitActions.js'
import { borderingArea } from './stepAI.js'

function moveAI(city, state, stat) {
  const ru = state.filter((region) => region.country === 'Russia')
  const uk = state.filter((region) => region.country === 'Ukraine')

  console.log(borderingArea(uk, ru, state))

  const border = borderingArea(uk, ru, state).find(area => area[0].id === city.id)

  console.log(border)

  if (border !== undefined) {
    //console.log(border)
    console.log(alliesBorder(city, uk))
    const neighboursAreas = alliesBorder(city, uk)

    const forMove = [];

    neighboursAreas.map((neighbour) => {
      const trueNeighbour = borderingArea(uk, ru, state).find(area => area[0].id === neighbour.id)
      if (trueNeighbour === undefined) {
        forMove.push(neighbour);
      }
    })

    if (forMove.length > 0) {
      return moveArmy(forMove[0], city)
    }



  }
}

function moveArmy(forMove, toMove, twoMovie) {
  /*   console.log([forMove, toMove])
    console.log('twomove', twoMovie ? true : false) */

  const newData = [{
    ...forMove,
    army: {
      ...forMove.army,
      foot: Math.round(twoMovie ? forMove.army.foot / 4 + 5 : forMove.army.foot / 2 + 5),
      artillery: Math.round(twoMovie ? forMove.army.artillery / 4 : forMove.army.artillery / 2),
      aviation: Math.round(twoMovie ? forMove.army.aviation / 4 : forMove.army.aviation / 2),
      pvo: Math.round(twoMovie ? forMove.army.pvo / 4 : forMove.army.pvo / 2),
    },
  }, {
    ...toMove,
    army: {
      ...toMove.army,
      foot: Math.round(twoMovie ? toMove.army.foot + (forMove.army.foot / 4 * 3) : forMove.army.foot / 2 + 5),
      artillery: Math.round(twoMovie ? toMove.army.artillery + (forMove.army.artillery / 4 * 3) : forMove.army.artillery / 2),
      aviation: Math.round(twoMovie ? toMove.army.aviation + (forMove.army.aviation / 4 * 3) : forMove.army.aviation / 2),
      pvo: Math.round(twoMovie ? toMove.army.pvo + (forMove.army.pvo / 4 * 3) : forMove.army.pvo / 2),
    }
  }];

  return newData;

}

function alliesBorder(city, uk) {

  const alliesAreas = [];

  const permit = permitActions[city.id];

  uk.map((ukArea) => {
    if (permit.includes(ukArea.id)) {
      alliesAreas.push(ukArea);
    }
  }
  );
  console.log(alliesAreas)

  const sortAlliesAreas = alliesAreas.sort(compareSum)

  function compareSum(a, b) {
    return (
      (b.army.foot + b.army.artillery + b.army.aviation + b.army.pvo) -
      (a.army.foot + a.army.artillery + a.army.aviation + a.army.pvo)
    )
  }

  return sortAlliesAreas;
}

export { moveAI, moveArmy };
