import { createSlice } from "@reduxjs/toolkit"
import { maindata } from '../../data/maindata.js'

export const mainSlice = createSlice({
  name: 'stateSlice',
  initialState: JSON.parse(window.localStorage.getItem('rukrStateMain01')) || maindata,
  reducers: {
    test_0: (state) => {
      state[0].army.foot += 1;
    },
    resetMState: (state) => {
      return state = maindata
    },
    scoutOpen: (state, action) => {
      const id = action.payload;
      const newState = state.map(area => {
        if (area.id === id) {
          return {
            ...area,
            army: {
              ...area.army,
              scouting: true,
            }
          }
        } else {
          return area;
        }

      })
      return newState;
    },
    setScout: (state) => {
      state.map(area => {
        area.army.scouting = false;
      })
    },
    newMainState: (state, action) => {
      state = action.payload;
    },
    activeLand: (state, action) => {
      const id = action.payload;
      state.map(oneState => {
        if (oneState.id === id) {
          const filtered = state.filter(item => item.country === oneState.country);
          filtered.map(everyState => {
            everyState.active = false;
            oneState.active = true;
          })
        }
      }
      )
    },
    goMoveArmy: (state, action) => {
      const id = action.payload.id;
      const army = action.payload.army;

      return state.map(area => {
        if (area.id === id) {
          return {
            ...area,
            army
          }
        }
        return area;
      })
    },
    goAttackArmy: (state, action) => {

      const id = action.payload.id;
      const army = action.payload.army;

      return state.map(area => {
        if (area.id === id) {
          return {
            ...area,
            army
          }
        }
        return area;
      })
    },
    changeArea: (state, action) => {
      console.log(action.payload)
      return state.map(area => {
        if (area.id === action.payload) {
          console.log(area.country)
          if (area.country === "Ukraine") {
            return {
              ...area,
              "country": "Russia",
              "active": false,
            }
          } else if (area.country === "Russia") {
            return {
              ...area,
              "country": "Ukraine",
              "active": false,
            }
          }



        }
        return area;
      })

    },
    swapArmy: (state, action) => {
      const [attackerId, exDeferId] = action.payload;
      const attackArea = state.find(area => area.id === attackerId)
      const defArea = state.find(area => area.id === exDeferId)

      if (attackArea && defArea) {
        const attackArmy = attackArea.army;
        const defArmy = defArea.army;
        attackArea.army = defArmy;
        defArea.army = attackArmy;

      }
    },
    rocketAttackD: (state, action) => {
      const newArmy = action.payload[0];
      const id = action.payload[1];

      console.log(action.payload)

      return state.map(area => {
        if (area.id === id) {
          return {
            ...area,
            army: {
              ...area.army,
              foot: newArmy.foot,
              artillery: newArmy.artillery,
              aviation: newArmy.aviation,
              pvo: newArmy.pvo,
            }
          }
        }
        return area;
      })

    },
    stage2Ru: (state, action) => {
      const id = action.payload;
      console.log(action.payload)
      const newState = state.map(area => {
        if (area.id === id) {
          return {
            ...area,
            "country": "Russia",
          }
        } else {
          return area;
        }

      })
      return newState;

    },
    stage2Uk: (state, action) => {
      const id = action.payload;
      const newState = state.map(area => {
        if (area.id === id) {
          return {
            ...area,
            army: {
              ...area.army,
              foot: area.army.foot + 3000,
              artillery: area.army.artillery + 700,
              aviation: area.army.aviation + 600,
              pvo: area.army.pvo + 500
            }
          }
        } else {
          return area;
        }
      })
      return newState;
    }
  }
})

export const { test_0, resetMState, newMainState, activeLand, goMoveArmy, goAttackArmy, changeArea, swapArmy, rocketAttackD,
  stage2Ru, stage2Uk, scoutOpen, setScout } = mainSlice.actions;
export default mainSlice.reducer;
