import React, { useEffect, useState } from 'react'

import { useSelector, useDispatch } from 'react-redux';
import { spiritUp, spiritDown, spiritEnemyUp, spiritEnemyDown } from '../../../../../store/slices/statSlice';

import useSound from 'use-sound';

import win from '../../../../../assets/sound/win.mp3'
import lose from '../../../../../assets/sound/lose.mp3'

import ads from '../../../../../assets/icons/int_icons/ads+1f.png'

import cs from './DefaultModal.module.scss'

export const DefaultModal = ({ defaultPopUp, setDefaultPopUp, lng, switchLng }) => {

  const [localPermit, setLocalPermit] = useState(false)

  const [winPlay] = useSound(win);
  const [losePlay] = useSound(lose);

  const [disBtn, setDisBtn] = useState(false);


  const stat = useSelector(state => state.stat)
  const dispatch = useDispatch();



  /* useEffect(() => {
    if (localPermit) {
      console.log('iswin:', defaultPopUp.isWin)
      if (defaultPopUp.isWin && stat.settings.sound) {
        winPlay()
      } else if (!defaultPopUp.isWin && stat.settings.sound) {
        losePlay()
      }
    }
  }, [localPermit]) */



  useEffect(() => {
    //defaultPopUp.isWin ? dispatch(spiritUp()) : dispatch(spiritDown());



    if (!defaultPopUp.isWin && defaultPopUp.type === 'enemy') {
      dispatch(spiritDown())
      dispatch(spiritEnemyUp())


    } else if (!defaultPopUp.isWin) {
      dispatch(spiritDown())
      dispatch(spiritEnemyUp())


    } else {


      dispatch(spiritUp())
      dispatch(spiritEnemyDown())
    }



    setLocalPermit(true)

  }, []);

  function upSpirit() {


    ysdk.adv.showRewardedVideo({
      callbacks: {
        onOpen: () => {
          console.log('Video ad open.');

        },
        onRewarded: () => {
          console.log('Rewarded!');
          dispatch(spiritUp())

          setDefaultPopUp((prev) => ({ ...prev, active: false }))
          setDisBtn(false)
        },
        onClose: () => {
          console.log('Video ad closed.');
          setDisBtn(false)

        },
        onError: (e) => {
          console.log('Error while open video ad:', e);
          setDisBtn(false)
          setDefaultPopUp((prev) => ({ ...prev, active: false }))
          /* dispatch(spiritUp())
          setDefaultPopUp((prev) => ({ ...prev, active: false })) */
        }
      }
    })



  }

  console.log(defaultPopUp)

  return (
    <div className={cs.defmodal}>
      <h1 className={!defaultPopUp.isWin ? cs.opponent_city
        : cs.you_city}>{defaultPopUp.title}</h1>
      <h2>{switchLng('titleReport')}:</h2>
      <div className={cs.losses_table}>

        <div className={cs.you}>
          <h3 className={defaultPopUp.type === 'enemy'
            ? cs.opponent_city : cs.you_city}>{defaultPopUp.areas[0]}</h3>
          <br />
          <div>{switchLng('living')}: -{defaultPopUp.data[0].foot}{lng === 'en' ? 'k' : '.'}</div>
          <div>{switchLng('tech')}: -{defaultPopUp.data[0].technic} </div>
          <div>{switchLng('volunteers')}: {defaultPopUp.isWin ? `+5${lng === 'en' ? 'k' : ''}` : '+0'}</div>
          <div>{switchLng('spiritUp')}: {defaultPopUp.isWin ? '+1%' : !defaultPopUp.isWin && defaultPopUp.type === 'enemy'
            ? '+1%' : '-1%'}</div>

        </div>
        <br />


        <div className={cs.opponent}>
          <h3 className={defaultPopUp.type === 'enemy'
            ? cs.you_city
            : cs.opponent_city}>{defaultPopUp.areas[1]}</h3>
          <br />

          <div>{switchLng('living')}: -{defaultPopUp.data[1].foot}{lng === 'en' ? 'k' : '.'}</div>
          <div>{switchLng('tech')}: -{defaultPopUp.data[1].technic} </div>
          <div>{switchLng('volunteers')}: +0</div>
          <div>{switchLng('spiritUp')}: {!defaultPopUp.isWin && defaultPopUp.type === 'enemy' ? '-1%' : !defaultPopUp.isWin ? '+1' : '-1%'}</div>

        </div>
        <br />

      </div>
      <div className={cs.buttons}>

        <button
          onClick={() => setDefaultPopUp((prev) => ({ ...prev, active: false }))}
          className={cs.btn_ok}>{defaultPopUp.btn}
        </button>

        {!defaultPopUp.isWin && <button
          disabled={true} // if for ads 'disBtn'!!!
          onClick={() => {
            setDisBtn(true)
            upSpirit()


          }
          }
          className={cs.btn_ok + ' ' + cs.btn_speech}>{switchLng('speechForArmy')}{/*  */}
          {/* <img
            style={{ width: '1em', height: '1em', margin: ' 0 0 -2px 2px' }}
            src={ads} /> */}
        </button>}
      </div>
    </div >
  )
}
